<template>
    <div class="modal fade" id="leaveGroup" tabindex="-1" aria-labelledby="exampleModalLabel1" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    {{group.name}} 
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                     {{group.name}}  ?<br>
                          <br>
                    (           .)
                </div>
                <div class="modal-footer">
                    <button class="btn btn-dark" data-bs-dismiss="modal" aria-label="Close" @click="leaveGroup"></button>
                    <button class="btn btn-dark" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import { Modal } from 'bootstrap';
export default {
    data() { 
        return {
            
        }
    },
    props: {
        group: {
            type: Object,
            required: true
        }
    },
    created() {
        
    },
    methods: {
        leaveGroup() {
            axios.post("/api/group/leave/" + this.group.groupSequence, {}, {
                headers: {
                    Authorization: `Bearer `+this.$cookies.get('accessToken'),
                }
            }).then(() => {
                if(this.$cookies.get("group").groupSequence == this.group.groupSequence) {
                    this.$cookies.remove("group")
                }
                const modalInstance = Modal.getInstance(document.getElementById('leaveGroup'))
                if (modalInstance) modalInstance.hide()
                    this.$router.push("/groups")
                });
                
            }
        }
    }
</script>
