<template>
    <div class="b-container">
        <h2 class="title pb-4 mb-4 fst-italic border-bottom"></h2>
        <form @submit.prevent="submitForm">
            <div class="input-group mb-3">
                <span class="input-group-text" id="basic-addon1"></span>
                <input type="text" class="form-control" v-model="id" aria-describedby="basic-addon1">
            </div>
            <div class="input-group mb-3">
                <span class="input-group-text" id="basic-addon1"></span>
                <input type="password" class="form-control" v-model="pwd" aria-describedby="basic-addon1">
            </div>
                <button type="submit" class="btn btn-dark btn-block"></button>
                <br>
        </form>     
        <div class="d-flex justify-content-between align-items-center">
            <div class="d-flex align-items-center">
                <input type="checkbox" id="saveId" class="form-check-input me-2" v-model="idSave">
                <label for="saveId"> </label>
            </div>
            <div>
                <span class="clickable-text mx-3"> </span>
                <span class="clickable-text"> </span>
            </div>
        </div>
        <div class="d-flex align-items-center">
            <span class="clickable-text join" @click="join"> </span>
        </div>
    </div>
</template>
<script>
    import axios from 'axios'
    export default {
        name: 'loginPage',
        data() {
            return {
                id:null,
                pwd:null,
                returnUrl:null,
                idSave:null
            }
        },
        created() {
            if(this.$cookies.get("accessToken")!=null){
                this.$toastr.warning(" .")
                this.$router.go(-1);
            }
            if(this.$cookies.get("saveId") !==null) {
                this.idSave=true
                this.id = this.$cookies.get("saveId")
            }
            
        },
        methods: {
            submitForm() {
                if (this.id == null || this.id === undefined || this.pwd == null || this.pwd === undefined) {
                    this.$toastr.warning("    .");
                } else {
                    const param = {
                        id: this.id,
                        password: this.pwd
                    };
                    axios.post('/api/user/login', param)
                        .then(response => {
                            const token = response.data.data.token;
                            const accessToken = token.accessToken; 
                            const refreshToken = token.refreshToken; 

                            if (!accessToken || !refreshToken) {
                                this.$toastr.warning("   .");
                                return;
                            }

                            this.$cookies.set('accessToken', accessToken);
                            this.$cookies.set('refreshToken', refreshToken);
                            this.$cookies.set('id', response.data.data.id);
                            this.$cookies.set('sequence', response.data.data.sequence);
                            if(this.idSave) {
                                this.$cookies.set("saveId", response.data.data.id)
                            } else {
                                this.$cookies.remove("saveId")
                            }
                            this.$emit("isLoginChange", true)
                            this.id = null
                            this.pwd = null
                            if (this.$cookies.get('beforePage') != null) {
                                this.$cookies.remove('beforePage');
                                this.$router.push("/");
                            } else {
                                this.$router.go(-1);
                            }
                        })
                        .catch(error => {
                            if (error.response) {
                                this.$toastr.error(error.response.data.message);
                                this.id = this.$cookies.get("saveId")
                                this.pwd = null
                            } else {
                                this.$toastr.warning("  .   .");
                            }
                        });
                }
            },
            join() {
                this.$router.push("/join")
            }
        }
    }

</script>
<style>
.clickable:hover {
  cursor: pointer;
}
.clickable-text {
    cursor: pointer; /*     */
    color: #000000; /*   (   ) */
}

.clickable-text:hover {
    color: #696969; /*     */
}
.join {
    margin-top: 20px; /*    */
    text-align: center;
    width: 100%; /*      */
}
</style>