    position: absolute;
    left: 30px;
    top: 52%; /*   */
    transform: translateY(-50%);
    color: gray; /*     */
    pointer-events: none; /*   */
    transition: 0.2s ease all;
    text-align: right; /*    */
}
.placeholder-label2 {
    position: absolute;
    left: 30px;
    top: 70%; /*   */
    transform: translateY(-50%);
    color: gray; /*     */
    pointer-events: none; /*   */
    transition: 0.2s ease all;
    text-align: right; /*    */
}
.placeholder-label3 {
    position: absolute;
    left: 30px;
    top: 80%; /*   */
    transform: translateY(-50%);
    color: gray; /*     */
    pointer-events: none; /*   */
    transition: 0.2s ease all;
    text-align: right; /*    */
}
