<template>
    <div class="b-container">
        <h2 class="title pb-4 mb-4 fst-italic border-bottom"> </h2>
        <div class="duplicateMessage-error" v-if="idIsDuplicate!=null && idIsDuplicate">  ID.</div>
        <div class="duplicateMessage-success" v-if="idIsDuplicate!=null && !idIsDuplicate">  ID.</div>
        <div v-if="idIsDuplicate==null"><div class="duplicate-message-space"></div></div>
        <div class="form-group verification-group">
            <input type="text" id="userId" v-model="userId" class="form-control" placeholder="ID" />
            <button @click="idCheck" class="btn btn-dark btn-sm"> </button>
        </div>
        <div class="message"> 8  .<span class="success" v-if="password != null && password.length >= 8"></span><span class= "error" v-if="password != null && password.length<8"></span></div>
        <div class="form-group">
            <input type="password" id="password" v-model="password" class="form-control" placeholder="" />
        </div>
        <div v-if="passwordConfirmError==null || passwordConfirmError==false"><div class="duplicate-message-space"></div></div>
        <div v-if="passwordConfirmError" class="duplicateMessage-error">  .</div>
        <div class="form-group">
            <input type="password" id="comfirmPassword" v-model="comfirmPassword" class="form-control" placeholder=" " />
        </div>
        <div class="duplicateMessage-error" v-if="emailConfirmError!=null && emailConfirmError">  .</div>
        <div class="duplicateMessage-error" v-if="emailIsDuplicate!=null && emailIsDuplicate">  .</div>
        <div class="duplicateMessage-success" v-if="emailIsDuplicate!=null && !emailIsDuplicate"> .     </div>
        <div class="duplicate-message-space" v-if="emailIsDuplicate==null && (emailConfirmError == null || !emailConfirmError)"></div>
        <div class="form-group verification-group">
            <input type="email" id="email" v-model="email" class="form-control" placeholder="" />
            <button v-if="!emailCodeSend" @click="emailCheck" class="btn btn-dark btn-sm"> </button>
            <button v-if="emailCodeSend && !verifcationSuccess" @click="emailCheck" class="btn btn-dark btn-sm"></button>
            <button v-if="emailCodeSend && verifcationSuccess" @click="emailCheck" class="btn btn-dark btn-sm" disabled></button>
        </div>
        <div class="duplicateMessage-success" v-if="verifcationSuccess!=null && verifcationSuccess"> </div>
        <div class="duplicateMessage-error" v-if="verifcationSuccess!=null && !verifcationSuccess">.  .</div>
        <div class="duplicate-message-space" v-if="verifcationSuccess==null"></div>
        <div class="form-group verification-group">
            <input type="text" id="verificationCode" v-model="verificationCode" class="form-control" placeholder="" />
            <button v-if="this.emailCodeSend != null & this.emailCodeSend != false & !this.verifcationSuccess" @click="sendVerification" class="btn btn-dark btn-sm"></button>
            <button v-if="this.emailCodeSend != true | this.verifcationSuccess" @click="sendVerification" class="btn btn-dark btn-sm" disabled></button>
        </div>

        <div v-if="verifcationSuccess !=null & verifcationSuccess && idIsDuplicate != null & !idIsDuplicate && emailIsDuplicate != null & !emailIsDuplicate && passwordConfirmError != null & !passwordConfirmError">
            <button @click="signup" class="btn btn-dark signup-btn"></button>
        </div>
        <div v-else>
            <button @click="signup" class="btn btn-dark signup-btn" disabled></button>
        </div>
    </div>
</template>
<script>
import axios from '@/js/axios';

export default ({
    data() {
        return {
            userId:null,
            password: null,
            comfirmPassword: null,
            email: null,
            verificationCode: null,
            verifcationSuccess: null,
            idIsDuplicate: null,
            emailIsDuplicate: null,
            passwordConfirmError: false,
            emailConfirmError: null,
            emailCodeSend: false
        }
    },
    props: {
        isLogin: {
            type: Boolean,
            required: true
        }
    },
    created() {
        if(this.isLogin) {
            this.$router.push("/")
            this.$toastr.warning(" .")
        }
    },
    watch: {
        userId(){
            this.idIsDuplicate=null
        },
        comfirmPassword() {
            if(this.comfirmPassword != this.password) {
                this.passwordConfirmError=true
            } else {
                this.passwordConfirmError = false
            }
        },
        email() {
            const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (emailPattern.test(this.email)) {
                this.emailConfirmError = false
            } else {
                this.emailConfirmError = true
            }
            this.emailIsDuplicate = null
            this.verifcationSuccess = null
            this.emailCodeSend = false
        },
        password() {
            if(this.comfirmPassword != this.password) {
                this.passwordConfirmError=true
            } else {
                this.passwordConfirmError = false
            }
        },
    },
    methods: {
        signup() {
            if(!this.verifcationSuccess | this.idIsDuplicate || this.emailIsDuplicate || this.passwordConfirmError) {
                this.$toastr.warning("     .")
            } else {
                axios.post("/api/user/join", {
                    "id":this.userId,
                    "password": this.password,
                    "email":this.email
                })
                this.$toastr.success(" .")
                this.$router.push("/")
            }
        },
        idCheck(){
            if(this.userId==undefined || this.userId == null || this.userId=="" || this.userId==" "){
                this.$toastr.warning(" ")
            }else{
                axios.get("/api/user/check/id/"+this.userId)
                .then((e) => {
                            if(e.data.data){
                                this.idIsDuplicate=false
                            }else{
                                this.idIsDuplicate=true
                            }
                        })
            }
        },
        emailCheck(){
            if(this.email==undefined || this.email == null || this.email=="" || this.email==" "){
                this.$toastr.warning(" ")
            } else if(this.emailConfirmError) {
                this.$toastr.warning("   .")
            } else if(this.verifcationSuccess) {
                this.$toastr.warning("   .")
            } else {
                axios.get("/api/user/check/email/"+this.email)
                .then((e) => {
                            if(e.data.data){
                                this.emailIsDuplicate=false
                                this.emailCodeSend = true
                                axios.post("/api/email/send?email="+this.email)
                            }else{
                                this.emailIsDuplicate=true
                            }
                        })
            }
        },
        sendVerification() {
            if(this.verificationCode == null) {
                this.$toastr.warning(" .")
            } else if(this.emailConfirmError || this.emailIsDuplicate) {
                this.$toastr.warning("   .")
            } else if(this.verifcationSuccess) {
                this.$toastr.warning("  .")
            }else {
                axios.post("/api/email/verify", {
                "email": this.email,
                "verifyCode": this.verificationCode
            }).then(e => {
                if(e.data.data) {
                    this.verifcationSuccess = true
                } else {
                    this.verifcationSuccess = false
                }
            })
            }
            
        }
    }
})
</script>

<style scoped>
.signup-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
}

.form-group {
  margin-bottom: 30px;
}

.verification-group {
  display: flex;
  gap: 10px;
}

.signup-btn {
  width: 100%;
}
.form-control {
    background-color: #f0f0f0;
    outline-style: solid;
    outline-color: #d7d7d7;
    height: 50px;
    border-radius: 15px;
}
.btn-sm {
    margin-top: 2px;
    margin-bottom: 2px;
    margin-left: 10px;
    width: 120px;
    border-radius: 15px;
}
.duplicateMessage-error {
    color: rgb(224, 100, 100);
    font-size: 14px;
}
.duplicateMessage-success {
    color: rgb(86, 186, 240);
    font-size: 14px;
}
.duplicate-message-space {
  height: 21px; /*    */
}
.message {
    color: rgb(127, 127, 127);
    font-size: 14px;
}
.success {
    width: 10px;            /*   */
    height: 20px;
    background-color: rgb(86, 186, 240); /*  */
    border-radius: 2px;     /*   */
    font-size: 11px;        /*   */
    color: #ffffff;            /*   */
    margin: 5px 7px;        /*   */
    margin-bottom: 20px;
}
.error {
    width: 10px;            /*   */
    height: 20px;
    background-color: rgb(224, 100, 100); /*  */
    border-radius: 2px;     /*   */
    font-size: 11px;        /*   */
    color: #ffffff;            /*   */
    margin: 5px 7px;        /*   */
    margin-bottom: 20px;
}
</style>